import React, {Component} from 'react';
import {Select, Input, Button, message} from 'antd';
import {reqAddQuestions, reqGetPublic} from '../../api';
import {questionType, gradeType, courseType, complexityType, yearType} from './Variable';
import {reqGetYears, reqGetLabel, reqGetGrade, reqGetCourse, reqGetKnowledges} from '../../api';
const {TextArea} = Input;

const {Option} = Select;

class AddPaper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      questionsType: 1,
      grade: gradeType[0].key,
      course: courseType[0].key,
      complexity: 1,
      year: yearType[0],
      questionsStart: '',
      questionsAnalyze: '',
      questionsAnswer: '',
      questionsRemark: '',
    };
    console.log('constructor');
  }

  componentDidMount() {
    console.log('componentDidMount');
  }

  handleChange = (key, value) => {
    this.setState({[key]: value});
  };

  handleAddPaper = async () => {
    const params = {...this.state};
    let find = Object.keys(params).find((key) => !params[key]);
    if (find) {
      console.log(find);
      message.error(find + '未填写');
      return;
    }
    params.label = '1';
    params.knowledge = '1';
    let result = await reqAddQuestions(params);
    if (result.status === 200) {
      message.success(result.message);
    } else {
      message.error(result.message);
    }
  };

  render() {
    const {complexity, course, grade, knowledge, label, year, questionsType} = this.state;
    return (
      <div className='paper'>
        <div style={{display: 'flex', flexWrap: 'wrap'}}>
          <div className='paper-card'>
            <span className='paper-span'>题型</span>
            <Select
              value={questionsType}
              style={{width: 120}}
              onChange={(value) => this.handleChange('questionsType', value)}
            >
              {questionType.map((t, index) => {
                return (
                  <Option value={index + 1} key={index + 1}>
                    {t}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div className='paper-card'>
            <span className='paper-span'>年级</span>
            <Select value={grade} style={{width: 120}} onChange={(value) => this.handleChange('grade', value)}>
              {gradeType.map((t) => {
                return (
                  <Option value={t.key} key={t.key}>
                    {t.value}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div className='paper-card'>
            <span className='paper-span'>科目</span>
            <Select value={course} style={{width: 120}} onChange={(value) => this.handleChange('course', value)}>
              {courseType.map((t) => {
                return (
                  <Option value={t.key} key={t.key}>
                    {t.value}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div className='paper-card'>
            <span className='paper-span'>难易程度</span>
            <Select
              value={complexity}
              style={{width: 120}}
              onChange={(value) => this.handleChange('complexity', value)}
            >
              {complexityType.map((t, index) => {
                return (
                  <Option value={index + 1} key={index + 1}>
                    {t}
                  </Option>
                );
              })}
            </Select>
          </div>
          <div className='paper-card'>
            <span className='paper-span'>年份</span>
            <Select value={year} style={{width: 120}} onChange={(value) => this.handleChange('year', value)}>
              {yearType.map((t) => {
                return (
                  <Option value={t} key={t}>
                    {t}
                  </Option>
                );
              })}
            </Select>
          </div>
        </div>
        <div style={{display: 'flex'}}>
          <div style={{width: '50%'}}>
            <div className='paper-Input'>
              <span className='paper-span'>题干</span>
              <TextArea className='input' onChange={(e) => this.handleChange('questionsStart', e.target.value)} />
            </div>
            <div className='paper-Input'>
              <span className='paper-span'>分析</span>
              <TextArea className='input' onChange={(e) => this.handleChange('questionsAnalyze', e.target.value)} />
            </div>
          </div>

          <div style={{width: '50%'}}>
            <div className='paper-Input'>
              <span className='paper-span'>答案</span>
              <TextArea className='input' onChange={(e) => this.handleChange('questionsAnswer', e.target.value)} />
            </div>
            <div className='paper-Input'>
              <span className='paper-span'>点评</span>
              <TextArea className='input' onChange={(e) => this.handleChange('questionsRemark', e.target.value)} />
            </div>
          </div>
        </div>
        <div style={{display: 'flex', marginTop: 20}}>
          <span className='foot-span'>解答</span>
          <TextArea className='TextArea' onChange={(e) => this.handleChange('questionsExplain', e.target.value)} />
        </div>
        <Button className='foot-btn' onClick={this.handleAddPaper}>
          添加
        </Button>
      </div>
    );
  }
}

export default AddPaper;
