import React, {Component} from 'react';
import {Input, Button, message} from 'antd';
import memoryUtils from '../../utils/memoryUtils';
import moment from 'moment';
import {reqAddTeach} from '../../api';
const {TextArea} = Input;

class AddPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      importArr: [
        {key: '考点一', label: '考点一'},
        {key: '考点二', label: '考点二'},
      ],
      user: memoryUtils.user,
      analyse: '剖析', //剖析
      conclude: '', //总结归纳
      consolidate: '自我巩固', //自我巩固
      course: '', //教案所属课程
      grade: '教案所属年级', //教案所属年级
      isAll: 1, //是否查看全部
      keyPoint: '', //考点突破-重点
      lessonsTime: moment().format('YYYY-MM-DD'), //上课时间
      points: '考点梳理', //考点梳理
      pointsQuestionIds: '小试牛刀', //小试牛刀-试题集合
      practiceQuestionIds: '教案试题的id集合', //教案试题的id集合
      prepareLessonsTime: moment().format('YYYY-MM-DD'), //备课时间
      review: '', //温故知新-复习
      seniorQuestionIds: '', //教案中考演练试题的id集合
      signature: '家长签字', //家长签字
      studentName: '学生名称', //学生名称
      studentQuestions: '', //学生主要问题
      teachGoal: '', //教学目标
      teachId: '教案id', //教案id
      teachName: '教学重点与难点', //教学重点与难点
      teachKeyDifficulty: '', //教师(教案作者名称)
      teachPhone: '110', //教师电话(教案归属唯一性保证)
      teachSource: '', //课题出处
      teachTopic: '', //教案课题
      teachType: '', //教案课型
      week: '1', //练习题属于周几
    };
    console.log(memoryUtils);
  }

  handleChange = (key, value) => {
    console.log(key, value);
    this.setState({
      [key]: value,
    });
  };

  handleCreatePlan = async () => {
    const params = {...this.state};
    delete params.importArr;
    delete params.user;
    let find = Object.keys(params).find((key) => !params[key]);
    if (find) {
      console.log(find);
      message.error(find + '未填写');
      return;
    }

    let result = await reqAddTeach(params);
    if (result.status === 200) {
      message.success(result.message);
    } else {
      message.error(result.message);
    }
  };

  render() {
    const {importArr, user} = this.state;
    return (
      <div className='addPlan'>
        <div className='addPlan-card'>
          <label className='card-title'>教案B</label>
          <div className='card-teacher'>
            <div className='teacher-detail' style={{marginLeft: 0}}>
              <span className='left' style={{height: '100%'}}>
                教师
              </span>
              <span className='create-teacher'>{user.name}</span>
            </div>
            <div className='course-detail' style={{display: 'flex'}}>
              <span className='left'>科目</span>
              <Input
                style={{padding: 0}}
                value={this.state.course}
                onChange={(e) => this.handleChange('course', e.target.value)}
              ></Input>
            </div>
            <div className='time-detail' style={{display: 'flex'}}>
              <span className='left'>备课时间</span>
              <Input
                style={{padding: 0}}
                value={this.state.prepareLessonsTime}
                // onChange={(e) => this.handleChange('prepareLessonsTime', e.target.value)}
              ></Input>
            </div>
          </div>
          <div className='card-source'>
            <div className='plan-topic' style={{display: 'flex'}}>
              <span className='left' style={{width: 40}}>
                课题
              </span>
              <Input
                style={{padding: 0}}
                value={this.state.teachTopic}
                onChange={(e) => this.handleChange('teachTopic', e.target.value)}
              ></Input>
            </div>
            <div className='plan-topic' style={{borderLeft: '1px solid #888', display: 'flex'}}>
              <span className='left' style={{width: 40}}>
                课型
              </span>
              <Input
                style={{padding: 0}}
                value={this.state.teachType}
                onChange={(e) => this.handleChange('teachType', e.target.value)}
              ></Input>
            </div>
          </div>
          <div className='plan-source'>
            <span>课题出处</span>
            <TextArea
              onChange={(e) => this.handleChange('teachSource', e.target.value)}
              className='TextArea'
              autoSize={{minRows: 2, maxRows: 5}}
            />
          </div>
          <div className='plan-source'>
            <span>学生主要问题</span>
            <TextArea
              onChange={(e) => this.handleChange('studentQuestions', e.target.value)}
              className='TextArea'
              autoSize={{minRows: 2, maxRows: 5}}
            />
          </div>
          <div className='plan-source'>
            <span>教学目标</span>
            <TextArea
              onChange={(e) => this.handleChange('teachGoal', e.target.value)}
              className='TextArea'
              autoSize={{minRows: 2, maxRows: 5}}
            />
          </div>
          <div className='plan-source'>
            <span>教学重点与难点</span>
            <TextArea
              onChange={(e) => this.handleChange('teachKeyDifficulty', e.target.value)}
              className='TextArea'
              autoSize={{minRows: 2, maxRows: 5}}
            />
          </div>
        </div>
        <div className='addPlan-content'>
          <div className='content-title'>教学内容</div>
          <div style={{display: 'flex'}}>
            <div className='content-left'>
              <div className='content-Input'>
                <span>1.【温故而知新】</span>
                <TextArea
                  onChange={(e) => this.handleChange('review', e.target.value)}
                  className='TextArea'
                  autoSize={{minRows: 5, maxRows: 15}}
                />
              </div>
              <div className='content-Input'>
                <span>2.【考点突破】</span>
                <TextArea
                  onChange={(e) => this.handleChange('keyPoint', e.target.value)}
                  className='TextArea'
                  autoSize={{minRows: 5, maxRows: 15}}
                />
              </div>
              {importArr.map((t, index) => {
                return (
                  <div className='content-Input' key={index}>
                    <span>{t.label}</span>
                    <TextArea
                      //   onChange={(e) => this.handleChange(t.key, e.target.value)}
                      className='TextArea'
                      autoSize={{minRows: 5, maxRows: 15}}
                    />
                  </div>
                );
              })}
              <div className='content-btn'>+</div>
              <div className='content-Input'>
                <span>3.【中考演练】</span>
                <TextArea
                  onChange={(e) => this.handleChange('seniorQuestionIds', e.target.value)}
                  className='TextArea'
                  autoSize={{minRows: 5, maxRows: 15}}
                />
                <span className='btn-span'>+添加试题</span>
              </div>
              <div className='content-Input'>
                <span>4.【总结归纳】</span>
                <TextArea
                  onChange={(e) => this.handleChange('conclude', e.target.value)}
                  className='TextArea'
                  autoSize={{minRows: 5, maxRows: 15}}
                />
              </div>
            </div>
            <div className='content-right'>
              <div className='right-Input'>
                <span className='title'>笔记</span>
                <TextArea
                  //   onChange={(e) => this.handleChange('笔记', e.target.value)}
                  className='TextArea'
                  style={{height: 'calc(100% - 31px)'}}
                />
              </div>
            </div>
          </div>
          <div className='content-title'>自我巩固</div>
          <div className='content-div'>
            <div className='content-Input'>
              <span>添加试题</span>
              添加试题 家长签字
            </div>
          </div>
          <div className='content-title'>每日一练</div>
          <div className='content-div'>
            <div className='content-Input'>
              <span>周一闯关习题</span>
              添加试题 家长签字
            </div>
            <div className='content-Input'>
              <span>周二闯关习题</span>
              添加试题 家长试题
            </div>
          </div>
        </div>
        <div className='foot'>
          <Button onClick={this.handleCreatePlan} style={{margin: 'auto', display: 'block'}}>
            创建教案
          </Button>
        </div>
      </div>
    );
  }
}

export default AddPlan;
