import React, {Component} from 'react';
import {Icon}  from 'antd'
import './style.less'

class CreateCard extends Component {
    render() {
        const {result} = this.props;
        return (<div className='plan-card'>
            <label className='card-title'>教案B</label>
            <div className='card-teacher'>
                <div className='teacher-detail' style={{marginLeft:0}}>
                    <span className='left'>教师</span>
                    <span className='create-teacher'>{result.teachName || ''}</span>
                </div>
                <div className='course-detail'>
                    <span className='left'>科目</span>
                    <span className='create-teacher'>{result.course || ''}</span>
                </div>
                <div className='time-detail'>
                    <span className='left'>备课时间</span>
                    <span className='create-teacher'>{result.lessonsTime || ''}</span>
                </div>
            </div>
            <div className='card-source'>
                <div className='plan-topic'>
                    <span className='left'>课题</span>
                    <span style={{marginLeft:10}}>{result.teachTopic||''}</span>
                </div>
                <div className='plan-topic' style={{borderLeft:'1px solid #888'}}>
                    <span className='left'>课型</span>
                    <span style={{marginLeft:10}}>{result.teachType||''}</span>

                </div>
            </div>
            <div className='plan-source'>
                <span>课题出处</span>
                <p>{result.teachSource||''}</p>
            </div>
            <div className='plan-source'>
                <span>学生主要问题</span>
                <p>{result.studentQuestions || ''}</p>
            </div>
            <div className='plan-foot'>
                <span>下载word格式 <Icon type='LeftOutlined' />  </span>
                <span>完整查看<Icon type='LeftOutlined' />  </span>
                <span>删除教案<Icon type='DownOutlined' />  </span>
            </div>
        </div>

        );
    }
}

export default CreateCard;