import React, {Component} from 'react';
import {Button} from 'antd';
import {showPlanType} from './Variable';
import TeacherTree from "../../components/TeacherTree";
import {reqGetLeftMenu, reqSearchTeach, reqGetAllTeaches, reqGetAllQuestions, reqGetQuestions} from "../../api";
import CreateCard from "../../components/CreateCrad";

class CreatePlan extends Component {
    constructor(props) {
        super(props);
        this.state={
            selectPlan:{},
            treeList:[],
            planType:{},
            result:[],
            grade:''
        }
    }

    async componentDidMount() {
        let result = await reqSearchTeach();
        let Leftresult = await reqGetLeftMenu();
        let newResult = await reqGetAllTeaches();
        console.log('--11-sf',result,Leftresult,newResult)
        if(Leftresult.data){
            this.setState({treeList:Leftresult.data})
        }
        if(newResult.data){
            this.setState({planType:newResult.data})
        }
        if(result.data){
            this.setState({result:result.data})
        }
    }

    handleClick = (key,d) => {
        const {selectPlan} = this.state;
        const newSelect = selectPlan[key]?[...selectPlan[key]]:[d];
        if(selectPlan[key]){
            const num = selectPlan[key]?selectPlan[key].indexOf(d):-1;
            if (num > -1) {
                newSelect.slice(num, 1)
            } else {
                newSelect.push(d)
            }
        }
        this.setState({selectPlan: {...selectPlan,[key]:newSelect}})
    }
    handleChange = (value) =>{
        // key,value
      this.setState({grade:value[0]},this.load)
    }

    async load (){
        const {grade,selectPlan} = this.state;
        const payload={
            ...selectPlan,
            grade,
        }
        const Result = await reqSearchTeach (payload)
        if(Result.data){
            this.setState({result:Result.data})
        }
    }
    render() {
        const {selectPlan,treeList,planType,result} = this.state;
        console.log('ddfg',planType)
        return (
            <div className='createPlan'>
                <div className="createPlan-left">
                    <Button>教案选取</Button>
                    <TeacherTree treeList={[...treeList]} onChange={this.handleChange}/>
                </div>
                <div className="createPlan-right">
                    <div className='plan-content'>
                        {Object.keys(showPlanType).map((t, index) => {
                            return <div className="tab-main" key={index}>
                                <span key={t} className="tab-main-text">{t}</span>
                                <div>
                                    {(planType[showPlanType[t]]&&planType[showPlanType[t]]||[]).map(d => {
                                        return <span
                                            key={`${t}-${d}`}
                                            className={`tab-main-span ${(selectPlan[t] || []).indexOf(d) > -1 ? 'selected' : ''}`}
                                            onClick={() => {
                                                this.handleClick(t, d)
                                            }}>
                                            {d}
                                        </span>
                                    })}
                                </div>
                            </div>
                        })}
                    </div>
                    <div className='plan-main'>
                        {result.map(it=>{
                            return <CreateCard result={it}/>
                        })}
                    </div>
                </div>
            </div>
        );
    }
}

export default CreatePlan;