import React, {Component} from 'react';
import HomeCard from './HomeCard';
import {reqGetPublic} from '../../api';
import '../style.less';
class Home extends Component {
  render() {
    return (
      <div className='teacher-manager'>
        <HomeCard
          title='试题'
          text='素材主要以试题为准'
          footBtn={[
            {text: '添加试题', link: '/add-paper'},
            {text: '查看试题', link: '/show-paper'},
          ]}
        />
        <HomeCard
          title='教案'
          text='素材主要以试题为准'
          footBtn={[
            {text: '查看教案', link: '/show-plan'},
            {text: '添加教案', link: '/add-plan'},
          ]}
        />
        <HomeCard title='试卷' text='素材主要以试题为准' footBtn={[{text: '创建试卷', link: '/'}]} />
      </div>
    );
  }
}

export default Home;
