import React, {Component} from 'react';
import {Button} from 'antd';
import {showPaperType} from './Variable';
import Main from './Main';
import TeacherTree from '../../components/TeacherTree';
import {reqGetAllQuestions, reqGetLeftMenu, reqGetQuestions} from '../../api';

class ShowPaper extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectPaper: {},
      treeList:[],
      topData:{
        easy:['全部','难','中等','简单','一般']
      },
      grade:'',
      ShowPaperResult:[]
    };
    this.payload={
      complexity:-1,
      course:null,
      grade:null,
      knowledge:null,
      label:null,
      questionsType:-1,
      year:null
    }
  }
  async componentDidMount() {
    let result = await reqGetLeftMenu();
    const showPaperResult = await reqGetQuestions (this.payload)
    let paperResult = await reqGetAllQuestions();
    if(result.data){
      this.setState({treeList:result.data})
    }
    if(paperResult.data){
      this.setState({topData:{...paperResult.data,easy:['全部','难','中等','简单','一般']}})
    }
    if(showPaperResult.data){
      this.setState({ShowPaperResult:showPaperResult.data})
    }
  }

  handleClick = (key, d) => {
    const {selectPaper} = this.state;
    const newSelect = selectPaper[key] ? [...selectPaper[key]] : [d];
    if (selectPaper[key]) {
      const num = selectPaper[key] ? selectPaper[key].indexOf(d) : -1;
      if (num > -1) {
        newSelect.slice(num, 1);
      } else {
        newSelect.push(d);
      }
    }
    this.setState({selectPaper: {...selectPaper, [key]: newSelect}},this.load);
  };
  handleChange = (value)=>{
      console.log(value)
    this.setState({grade:value[0]},this.load)
    // 改变查看状态，发送请求
  }

  async load (){
    const {grade,selectPaper,topData} = this.state;
    const payload={
      ...selectPaper,
      grade,
    }
    if(payload.easy){
      payload.easy=payload.easy.map(s=>{
        return topData.easy.indexOf(s)+1
      })
    }
    const showPaperResult = await reqGetQuestions (payload)
    if(showPaperResult.data){
      this.setState({ShowPaperResult:showPaperResult.data})
    }
  }
  render() {
    const {selectPaper,treeList,topData,ShowPaperResult} = this.state;
    return (
      <div className='showPaper'>
        <div className='showPaper-left'>
          <Button>素材选取</Button>
          <TeacherTree treeList={[...treeList]} onChange={this.handleChange}/>
        </div>
        <div className='showPaper-right'>
          <div className='showPaper-tab'>
            {Object.keys(showPaperType).map((t, index) => {
              return (
                <div className='tab-main' key={index}>
                  <span key={t} className='tab-main-text'>
                    {t}
                  </span>
                  <div>
                    {(topData[showPaperType[t]]&&topData[showPaperType[t]]||[]).map((d,shortIndex) => {
                      return (
                        <span
                          key={`${t}-${d}`}
                          className={`tab-main-span ${(selectPaper[t] || []).indexOf(d) > -1 ? 'selected' : ''}`}
                          onClick={() => {
                            this.handleClick(showPaperType[t], d);
                          }}
                        >
                          {d}
                        </span>
                      );
                    })}
                  </div>
                </div>
              );
            })}
          </div>
          <div className='showPaper-content'>
            <Main result={ShowPaperResult}/>
          </div>
        </div>
      </div>
    );
  }
}

export default ShowPaper;
