import React, {Component} from 'react';
import {Button} from 'antd';
import {reqGetQuestionsAnswer,reqAddTeachToMe} from '../../api';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selectPaper: {}
        }
    }
    handleClick = async (key,re,index) =>{
        const payload={
            questionsId:re.questionsId || index,
            ...re,
        }
        if(key === 'show'){
            const result = await reqGetQuestionsAnswer(payload);
            console.log('00999sf',result)
        }
        if(key === 'add'){
            const result = await reqAddTeachToMe(payload);
            console.log('00999sf',result)
        }

    }
    render() {
        const {result} = this.props;
        return (
            <div>
                {(result||[]).map((re,index)=>{
                    return <div key={index} className='content-main'>
                        <span style={{padding:10}}>{re.questionsStart}</span>
                        <div className='foot'>
                            <Button className='showButton' onClick={()=>this.handleClick('show',re,index)}>查看答案</Button>
                            <span className='addButton' onClick={()=>this.handleClick('add',re,index)}>+添加到我的</span>
                        </div>

                    </div>
                })}

            </div>

        );
    }
}

export default Main;