import React, {Component} from 'react';
import {Layout, Button} from 'antd';
import {withRouter} from 'react-router-dom';
import './style.less';

const {Header} = Layout;

class TeachHeader extends Component {
  handleClick = () => {
    this.props.history.replace('/');
  };

  handleCreateAccount = () => {
    // 跳转到创建账号
  };

  render() {
    const {
      location: {pathname},
    } = this.props;
    return (
      <Header>
        {pathname !== '/home' && <Button onClick={this.handleClick}>返回</Button>}
        {/* {pathname === '/home' && <Button className='teacher-header-btn'
                                                 style={{right:300}}
                                                 onClick={this.handleCreateAccount}>创建账号</Button>} */}
        <Button className='teacher-header-btn'>个人中心</Button>
      </Header>
    );
  }
}

export default withRouter(TeachHeader);
